.homesecWrap {
    .article1 { 
        // height: 50vw !important; 
        // height:100vh를 쓰면 편하지만(각각마다 높이를 지정 안해줘도 됨) 
        // 화면 넓이 기준에 맞추기 위해 50vw를 씀
    }
    .article2 { 
        .row {
            ul { display: flex; flex-wrap: wrap;
                li { width: 33.33%; padding: 20px;
                    a { display: flex;
                        img { width: 30%; margin-right: 10px;
                        border-radius: 10px; } 
                        div { align-self: center; }
                    }
                }
            }
            .lifeStyle {
                h2 { font-size: 20px; }
                ul { display: flex; 
                    li { width: 33.33%; padding: 0 10px; 
                        img { width: 100%; border-radius: 10px; } 
                    }
                }
            }
        }
    }
    .article3 { background: #000;
        color: #fff; 
        padding: 100px 0px;
        .row { display: flex; 
            div { width: 52%; 
                img { width: 100%; 
                    padding: 10px 10px 0px; 
                    border-radius: 20px 20px 0 0;
                    vertical-align: middle; }
                p { background: #999;
                    padding: 20px;
                    border-radius: 0 0 20px 20px;
                    margin: 0 10px;
                    font-size: 15px; }
            &.right { width: 48%; 
                display: flex; 
                flex-direction: column;
                justify-content: space-between;
                div { width: 100%; }
                .rightFst { display: flex; flex-direction: row; }
                }
            }
        }
    }
}