.headerWrap { position: fixed;
    top: 0; left: 0;
    width: 100%;
    color: #fff;
    transition: all 0.5s;
    z-index: 99;
    .row { position: relative;
        display: flex; height: 80px;
        justify-content: space-between;
        align-items: center;
        // .logo, .nav, .mobileNav { align-self: center; } 
        .mobileNav { 
            // display: flex;
            // justify-content: flex-end; // 오른쪽 끝 정렬
            position: relative;
            button { padding: 10px 20px;
                border-radius: 50px;
                border: none; 
                font-size: 20px; 
                display: flex;
                cursor: pointer;
                .ham { margin-right: 10px; }
                .ham, .person { align-self: center; }
            }
            .mobileNavList { display: none;
                position: absolute;
                top: 100%; right: 0; 
                margin-top: 10px; // 작은 간격은 top값을 조절하는 것보단 margin의 px값으로 주는게 나음
                background: #fff; 
                padding: 10px 0; 
                color: #000;
                width: 150px; 
                border-radius: 10px;
                &.opener { display: block; } 
            }
                ul {
                    li { line-height: 30px; padding: 0 20px; }
                    li:nth-child(3) { border-top: 1px solid #ddd; 
                        padding-top: 10px;
                        margin-top: 10px; }
                }
        }
        .nav { position: absolute;
            left: 50%;
            transform: translate(-50%); 
            .depth1 { display: flex;
                li { position: relative; 
                    padding: 15px 0; 
                    cursor: pointer; 
                    margin: 0 30px;}
            }
            .depth2 { display: none;
                position: absolute;
                top: 100%; left: 50%;
                transform: translate(-50%);
                width: 150px;
                background: #fff;
                color: #000;
                padding: 20px 30px;
                border-radius: 10px;
                &.on { display: block; }
                li { padding: 10px 0px; margin: 0;}
            } 
        }
    }
}